.none{
    display:none;
}

#AppField{
    cursor: none;
    // background-color: #cbccf1;
    #posters{
        cursor: none;
        grid-area: 2/2/3/3;
        display: grid;
        grid-template-columns:1fr 1fr;
        gap:2%;
    
        #LeftPoster{
            cursor: none;
            grid-column-start:1;
        }
        #RightPoster{
            cursor: none;
            grid-column-start:2;
        }
    
        .poster{
            border-radius: 5%;
            font-size: clamp(14px, 1.85vw, 32px);
            font-family: 'Oswald-Regular';
            .pAppField{
                color:#cbccf1;
                padding:2.5%;
            }
        }
    
        .poster:hover{
            transition: 1000ms;
            background: #cbccf1;
            box-shadow:  29px 29px 57px #bebebe,
                        -29px -29px 57px #ffffff;
            .pAppField{
                color:#ffffff;
                transition: color 1000ms;
                -webkit-text-stroke: 0.5px #bebebe;
            }
        }

    }
// End POSTERS
    fieldset{
        @extend %StyleFieldset;
    }

    input[type='text'],
    textarea,
    select{
        @extend %StyleInputNickname;
        outline: none;
    }
    
    input[type='text']:focus,
    textarea:focus{
        @extend %StyleInputNickname;
        outline: none;
        box-shadow:
            -5px -5px 10px #a8a9c8,
            5px 5px 10px #eeefff;
    }

    input[type='text']::placeholder{
        color: #cbccf1;
        font-size:clamp(8px, 1.25vw, 16px);
        align-self: center;
    }
    
    textarea::placeholder{
        color: #cbccf1;
        font-size:clamp(8px, 1.5vw, 16px);
    }


    #fieldsetWrapper{
        margin-top:6px;
        display:grid;
        grid-template-rows:10%, repeat(5, 1fr);
    }

    #RangeBlack{
        @include StyleRange(white, black);
        @extend %StyleRange;
    }
// Style input range
    input[type=range]{
        height: 8px;
        appearance: none;
        background: linear-gradient(to right, white, black);
        border-radius: 50px;
        border: 1px solid #888;
        box-shadow:0 0 5px #cbccf1;
        overflow: hidden;
        outline:none;
    }
// Style Thumb - "POLZUNOK"
    input[type=range]::-webkit-slider-thumb{
        height: 8px;
        width: 17px;
        background: linear-gradient(to right, white, black);
        appearance: none;
        cursor: pointer;
        border-radius: 4px;
        box-shadow:-200px 150px 150px 185px #8689cf;
        border: 1px solid #eeefff
    }
    input[type=range]::-moz-range-thumb {
        height: 8px;
        width: 17px;
        background: linear-gradient(to right, white, black);
        appearance: none;
        cursor: pointer;
        border-radius: 4px;
        box-shadow:-200px 150px 150px 185px #7578c9;
        border: 1px solid #eeefff
    }
    .pRange{
        color:red;
        font-size: clamp(1rem, 3vw, 2.5rem);;
    }
    legend{
        font-size: clamp(8px, 1.5vw, 32px);
    }
}

//BUTTON
.BTN {
    margin:0.5vw;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    cursor: pointer;
    width: clamp(60px, 16vw, 100px);
    height: clamp(40px, 4vw, 80px);
    background-image: linear-gradient(to top, #D8D9DB 0%, #fff 80%, #FDFDFD 100%);
    border-radius: 30px;
    border: 1px solid #8F9092;
    transition: all 0.2s ease;
    font-family: "Source Sans Pro", sans-serif;
    font-size: clamp(4px, 4vw, 16px);
    font-weight: 600;
    color: #606060;
    text-shadow: 0 1px #fff;
  }
  
  .BTN:hover {
    box-shadow: 0 4px 3px 1px #FCFCFC, 0 6px 8px #D6D7D9, 0 -4px 4px #CECFD1, 0 -6px 4px #FEFEFE, inset 0 0 3px 3px #CECFD1;
  }
  
  .BTN:active {
    box-shadow: 0 4px 3px 1px #FCFCFC, 0 6px 8px #D6D7D9, 0 -4px 4px #CECFD1, 0 -6px 4px #FEFEFE, inset 0 0 5px 3px #999, inset 0 0 30px #aaa;
  }
  
  .BTN:focus {
    box-shadow: 0 4px 3px 1px #FCFCFC, 0 6px 8px #D6D7D9, 0 -4px 4px #CECFD1, 0 -6px 4px #FEFEFE, inset 0 0 5px 3px #999, inset 0 0 30px #aaa;
  }
