// background for Modal window
.modal{
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background-color: rgb(0,0,0,0.75);
    /*opacity:0;*/
    overflow: hidden;
    overflow-y: auto;
    transition: opacity 0.5s;
    z-index: 999;
    cursor: no-drop;
}

.modal-wrapper{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    cursor: no-drop;
}
.modal-content{
    position: relative;
    margin:20px;
    width: 100%;
    max-width: 600px;
    border-radius: 20px;
    background-image: url('./BLACK_II_desktop.jpg');
    padding:40px 20px 20px 20px;
    transform: translateY(-50px);
    transition: opacity 0.3s, transform 0.3s;
    color:rgb(117, 97, 97);
    cursor: no-drop;
}
.modal-close-button{
    position:absolute;
    top:6px;
    right: 6px;
    padding: 5px;
    background-color: transparent;
    border:none;
    cursor: pointer;
}

