#AppHeader {
    cursor: none;

    grid-area: 1/1/2/4;

    display: grid;
    grid-template-columns: 18% 18% 18% 18% 1fr;
    
    // *********
    .btnMenu {
        margin: 5%;
        text-align: center;
        display: grid;
        border-radius: 20px;
        background: #cbccf1;
        box-shadow:
            10px 10px 20px #a8a9c8,
            -10px -10px 20px #eeefff;
    }
    // ********
    #btnMenu0 {
        @include StyleBtnMenu(1);
        #pBtnMenu0 {
            @extend %StylePBtnMenu;
        }
    }
    #btnMenu0:hover {
        @extend %StyleBtnMenu_hover;
    }
    #btnMenu0:active {
        @extend %StyleBtnMenu_active;
    }
    // *******
    #btnMenu1 {
        @include StyleBtnMenu(2);
        #pBtnMenu1 {
            @extend %StylePBtnMenu;
        }
    }
    #btnMenu1:hover {
        @extend %StyleBtnMenu_hover;
    }
    #btnMenu1:active {
        @extend %StyleBtnMenu_active;
    }
    // ********
    #btnMenu2 {
        @include StyleBtnMenu(3);
        #pBtnMenu2 {
            @extend %StylePBtnMenu;
        }
    }
    #btnMenu2:hover {
        @extend %StyleBtnMenu_hover;
    }
    #btnMenu2:active {
        @extend %StyleBtnMenu_active;
    }
    // **********
    #btnMenu3 {
        @include StyleBtnMenu(4);
        #pBtnMenu3 {
            @extend %StylePBtnMenu;
        }
    }
    #btnMenu3:hover {
        @extend %StyleBtnMenu_hover;
    }
    #btnMenu3:active {
        @extend %StyleBtnMenu_active;
    }
}
