%StyleBtnMenu_hover {
    cursor: pointer;
    background: #c7c9f8;
    transition: 500ms;
    background: #cbccf1;
    box-shadow:
        -10px -10px 20px #a8a9c8,
        10px 10px 20px #eeefff;
}

%StyleBtnMenu_active {
    background-color: #b8baf5;
    box-shadow: 0 5px #666;
    transition: 50ms;
    transform: translateY(4px);
    transform: translateX(2px);
}

%StylePBtnMenu {
    margin-top: -15px;
    align-self: center;
    font-size: clamp(8px, 3vw, 80px);
    color: #ffffff;
}

%StyleInputNickname{
    width: clamp(25px, 14vw, 200px);
    margin: 1%;
    border: none;
    background-color: #ffffff;
    
}

%StyleFieldset{
    border:1px solid #ffffff;
    color:#ffffff;
}

%StyleRange{
    height: 4rem;
}