@import "../../node_modules/reset.css/reset.css";
@import "../../node_modules/normalize.css/normalize.css";
@font-face {
  font-family: "Oswald-Regular";
  src: url("./Oswald-Regular.ttf");
}
@font-face {
  font-family: "Oswald-Medium";
  src: url("./Oswald-Medium.ttf");
}
@font-face {
  font-family: "Oswald-SemiBold";
  src: url("./Oswald-SemiBold.ttf");
}
@font-face {
  font-family: "Oswald-Bold";
  src: url("./Oswald-Bold.ttf");
}
#AppHeader #btnMenu3:hover, #AppHeader #btnMenu2:hover, #AppHeader #btnMenu1:hover, #AppHeader #btnMenu0:hover {
  cursor: pointer;
  background: #c7c9f8;
  transition: 500ms;
  background: #cbccf1;
  box-shadow: -10px -10px 20px #a8a9c8, 10px 10px 20px #eeefff;
}

#AppHeader #btnMenu3:active, #AppHeader #btnMenu2:active, #AppHeader #btnMenu1:active, #AppHeader #btnMenu0:active {
  background-color: #b8baf5;
  box-shadow: 0 5px #666;
  transition: 50ms;
  transform: translateY(4px);
  transform: translateX(2px);
}

#AppHeader #btnMenu3 #pBtnMenu3, #AppHeader #btnMenu2 #pBtnMenu2, #AppHeader #btnMenu1 #pBtnMenu1, #AppHeader #btnMenu0 #pBtnMenu0 {
  margin-top: -15px;
  align-self: center;
  font-size: clamp(8px, 3vw, 80px);
  color: #ffffff;
}

#AppField input[type=text]:focus,
#AppField textarea:focus, #AppField input[type=text],
#AppField textarea,
#AppField select {
  width: clamp(25px, 14vw, 200px);
  margin: 1%;
  border: none;
  background-color: #ffffff;
}

#AppField fieldset {
  border: 1px solid #ffffff;
  color: #ffffff;
}

#AppField #RangeBlack {
  height: 4rem;
}

#newCursor {
  position: absolute;
  border: 1px solid #cbccf1;
  width: 20px;
  height: 20px;
  opacity: 0.5;
  border-radius: 10px;
  background: #cbccf1;
  box-shadow: 4px 4px 8px #adadcd, -4px -4px 8px #e9ebff;
}

body {
  background-color: #cbccf1;
}

#fon {
  height: 115vh;
  cursor: none;
  font-family: "Oswald-Regular";
  display: grid;
  grid-template: 17.5% 1fr 5%/18% 1fr 18%;
}

#AppHeader {
  cursor: none;
  grid-area: 1/1/2/4;
  display: grid;
  grid-template-columns: 18% 18% 18% 18% 1fr;
}
#AppHeader .btnMenu {
  margin: 5%;
  text-align: center;
  display: grid;
  border-radius: 20px;
  background: #cbccf1;
  box-shadow: 10px 10px 20px #a8a9c8, -10px -10px 20px #eeefff;
}
#AppHeader #btnMenu0 {
  grid-column-start: 1;
}
#AppHeader #btnMenu1 {
  grid-column-start: 2;
}
#AppHeader #btnMenu2 {
  grid-column-start: 3;
}
#AppHeader #btnMenu3 {
  grid-column-start: 4;
}
.none {
  display: none;
}

#AppField {
  cursor: none;
}
#AppField #posters {
  cursor: none;
  grid-area: 2/2/3/3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2%;
}
#AppField #posters #LeftPoster {
  cursor: none;
  grid-column-start: 1;
}
#AppField #posters #RightPoster {
  cursor: none;
  grid-column-start: 2;
}
#AppField #posters .poster {
  border-radius: 5%;
  font-size: clamp(14px, 1.85vw, 32px);
  font-family: "Oswald-Regular";
}
#AppField #posters .poster .pAppField {
  color: #cbccf1;
  padding: 2.5%;
}
#AppField #posters .poster:hover {
  transition: 1000ms;
  background: #cbccf1;
  box-shadow: 29px 29px 57px #bebebe, -29px -29px 57px #ffffff;
}
#AppField #posters .poster:hover .pAppField {
  color: #ffffff;
  transition: color 1000ms;
  -webkit-text-stroke: 0.5px #bebebe;
}
#AppField input[type=text],
#AppField textarea,
#AppField select {
  outline: none;
}
#AppField input[type=text]:focus,
#AppField textarea:focus {
  outline: none;
  box-shadow: -5px -5px 10px #a8a9c8, 5px 5px 10px #eeefff;
}
#AppField input[type=text]::placeholder {
  color: #cbccf1;
  font-size: clamp(8px, 1.25vw, 16px);
  align-self: center;
}
#AppField textarea::placeholder {
  color: #cbccf1;
  font-size: clamp(8px, 1.5vw, 16px);
}
#AppField #fieldsetWrapper {
  margin-top: 6px;
  display: grid;
  grid-template-rows: 10%, repeat(5, 1fr);
}
#AppField #RangeBlack {
  border-radius: 4px;
}
#AppField input[type=range] {
  height: 8px;
  appearance: none;
  background: linear-gradient(to right, white, black);
  border-radius: 50px;
  border: 1px solid #888;
  box-shadow: 0 0 5px #cbccf1;
  overflow: hidden;
  outline: none;
}
#AppField input[type=range]::-webkit-slider-thumb {
  height: 8px;
  width: 17px;
  background: linear-gradient(to right, white, black);
  appearance: none;
  cursor: pointer;
  border-radius: 4px;
  box-shadow: -200px 150px 150px 185px #8689cf;
  border: 1px solid #eeefff;
}
#AppField input[type=range]::-moz-range-thumb {
  height: 8px;
  width: 17px;
  background: linear-gradient(to right, white, black);
  appearance: none;
  cursor: pointer;
  border-radius: 4px;
  box-shadow: -200px 150px 150px 185px #7578c9;
  border: 1px solid #eeefff;
}
#AppField .pRange {
  color: red;
  font-size: clamp(1rem, 3vw, 2.5rem);
}
#AppField legend {
  font-size: clamp(8px, 1.5vw, 32px);
}

.BTN {
  margin: 0.5vw;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  cursor: pointer;
  width: clamp(60px, 16vw, 100px);
  height: clamp(40px, 4vw, 80px);
  background-image: linear-gradient(to top, #D8D9DB 0%, #fff 80%, #FDFDFD 100%);
  border-radius: 30px;
  border: 1px solid #8F9092;
  transition: all 0.2s ease;
  font-family: "Source Sans Pro", sans-serif;
  font-size: clamp(4px, 4vw, 16px);
  font-weight: 600;
  color: #606060;
  text-shadow: 0 1px #fff;
}

.BTN:hover {
  box-shadow: 0 4px 3px 1px #FCFCFC, 0 6px 8px #D6D7D9, 0 -4px 4px #CECFD1, 0 -6px 4px #FEFEFE, inset 0 0 3px 3px #CECFD1;
}

.BTN:active {
  box-shadow: 0 4px 3px 1px #FCFCFC, 0 6px 8px #D6D7D9, 0 -4px 4px #CECFD1, 0 -6px 4px #FEFEFE, inset 0 0 5px 3px #999, inset 0 0 30px #aaa;
}

.BTN:focus {
  box-shadow: 0 4px 3px 1px #FCFCFC, 0 6px 8px #D6D7D9, 0 -4px 4px #CECFD1, 0 -6px 4px #FEFEFE, inset 0 0 5px 3px #999, inset 0 0 30px #aaa;
}

#AppFooter {
  grid-area: 3/1/4/4;
}

#AppLeftSidebar {
  grid-area: 2/1/3/2;
}

#AppRightSidebar {
  grid-area: 2/3/3/4;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  /*opacity:0;*/
  overflow: hidden;
  overflow-y: auto;
  transition: opacity 0.5s;
  z-index: 999;
  cursor: no-drop;
}

.modal-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  cursor: no-drop;
}

.modal-content {
  position: relative;
  margin: 20px;
  width: 100%;
  max-width: 600px;
  border-radius: 20px;
  background-image: url("./BLACK_II_desktop.jpg");
  padding: 40px 20px 20px 20px;
  transform: translateY(-50px);
  transition: opacity 0.3s, transform 0.3s;
  color: rgb(117, 97, 97);
  cursor: no-drop;
}

.modal-close-button {
  position: absolute;
  top: 6px;
  right: 6px;
  padding: 5px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

